<template>
    <v-container>
      <v-row v-if="!loading">
        <v-col cols="12" lg="12" md="12" sm="12" class="text-left">
          <v-btn @click="back()">العودة <v-icon>mdi-arrow-left</v-icon></v-btn>
        </v-col>
        <v-col cols="12" lg="8" md="8" sm="12" class="">
          <MapShowOnly :points="points" />
        </v-col>
        <v-col cols="12" lg="4" md="4" sm="12" class="">
          <v-card rounded="lg" elevation="1" :loading="loading" class="mx-auto">
            <v-img cover height="250" :src="travelDetail.imageUrl"></v-img>
  
            <v-card-item>
              <v-card-title>{{ travelDetail.title }}</v-card-title>
  
              <v-card-subtitle>
                <span class="me-1"
                  >{{ travelDetail.places[0].address.country }} ,
                  {{ travelDetail.places[0].address.city }}</span
                >
  
                <v-icon
                  color="error"
                  icon="mdi-fire-circle"
                  size="small"
                ></v-icon>
              </v-card-subtitle>
            </v-card-item>
  
            <v-card-text>
              <v-row align="center" class="mx-0">
                <v-rating
                  :model-value="4.5"
                  color="amber"
                  density="compact"
                  half-increments
                  readonly
                  size="small"
                ></v-rating>
  
                <div class="text-grey ms-4">4.5 (413)</div>
              </v-row>
  
              <div class="my-4 text-subtitle-1">
                {{ travelDetail.price }} دينار عراقي للشخص
              </div>
  
              <div>
                من
  
                {{ travelDetail.dateFrom }}
                الى
                {{ travelDetail.dateTo }}
                <p class="pb-2">عدد الاشخاص : {{ travelDetail.memberNumber }}</p>
                <p class="text-grey">{{ travelDetail.description }}</p>
              </div>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" lg="12" md="12" sm="12">
          <v-card width="100%" class="mt-12" flat>
            <v-timeline
              width="100%"
              align="start"
              v-if="travelDetail.places.length > 0"
            >
              <v-timeline-item
                dot-color="grey"
                icon="mdi-layers-triple"
                fill-dot
              ></v-timeline-item>
              <v-timeline-item
                v-for="(item, index) in travelDetail.places"
                :key="index"
                dot-color="primary"
                size="small"
              >
                <v-card
                  color="grey-lighten-4"
                  class="pa-2"
                  rounded="lg"
                  :value="true"
                >
                  <v-card-title>{{
                    item.placeName ? item.placeName : "لا يوجد اسم"
                  }}</v-card-title>
                  <v-card-text class="text-grey"
                    >{{ item.address.district ? item.address.district : "" }} ,
                    {{ item.address.road ? item.address.road : "" }} ,
                    {{ item.address.state ? item.address.state : "" }} ,
                    {{ item.address.subdistrict ? item.address.subdistrict : "" }}
                  </v-card-text>
  
                  <v-card-subtitle>
                    <p>
                      عدد الايام : {{ item.nightsStayed ? item.nightsStayed : 0 }}
                    </p>
                  </v-card-subtitle>
  
                  <v-card
                    class="ma-2 pa-2"
                    rounded="lg"
                    v-for="i in item.nightsStayed"
                    :key="i"
                  >
                    <v-card-title>اليوم - {{ i }}</v-card-title>
                    <v-card-text>
                      <div
                        v-if="
                          item.activities.length == 0 ||
                          item.activities.length == '0'
                        "
                      >
                        لم يتم اضافة نشاطات
                      </div>
                      <div v-else v-for="list in item.activities" :key="list">
                        <v-alert
                          class="my-1"
                          :title="list.title"
                          border="start"
                          :text="list.time"
                          variant="tonal"
                          v-if="list.dayNum == i"
                        >
                          <p color="text-grey">{{ list.description }}</p>
                        </v-alert>
                      </div>
                    </v-card-text>
                  </v-card>
                </v-card>
              </v-timeline-item>
            </v-timeline>
            <p class="pt-4 pr-4 text-grey" v-else>لم يتم اختيار مواقع</p>
          </v-card>
        </v-col>
      </v-row>
      <div class="loading-container" v-if="loading">
        <v-progress-circular
          :size="70"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </div>
    </v-container>
  </template>
  <script setup>
  import { onMounted, ref } from "vue";
  import { useCounterStore } from "@/store/index.js";
  import { useRouter, useRoute } from "vue-router";
  import { db } from "../../firebase.js";
  import { getDocs, collection, doc, getDoc } from "firebase/firestore";
  import MapShowOnly from "../../components/mapShowOnly";
  
  const points = ref([]);
  const router = useRouter();
  const route = useRoute();
  const plans = ref([]);
  const loading = ref(true);
  const travelDetail = ref({});
  onMounted(async () => {
    try {
      const desiredId = route.params.id;
      const planCollection = await getDocs(collection(db, "travelPlans"));
      const promises = planCollection.docs.map(async (plan) => {
        if (plan.id === desiredId) {
          const planDoc = await getDoc(doc(db, "travelPlans", plan.id));
          if (planDoc.exists()) {
            plans.value.push({ ...planDoc.data(), id: plan.id });
            travelDetail.value = planDoc.data();
            travelDetail.value.places.forEach((e) => {
              points.value.push({
                lat: e.latitude,
                lng: e.longitude,
              });
            });
  
            loading.value = false;
          } else {
            console.log("Document not found");
          }
        }
      });
  
      await Promise.all(promises);
    } catch (error) {
      console.error("Error fetching plan:", error);
    }
  });
  
  function back() {
    router.go(-1);
  }
  </script>
  <style>
  .loading-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80vh;
  }
  </style>
  